.grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 1fr 2fr;
  grid-row-gap: 3rem;
  grid-column-gap: 2rem;
  grid-template-columns: 1fr 1fr 1fr;
}

.grid-5 {
  display: grid;
